/* Estilos generales del formulario */
/* Estilos generales del formulario */
.form-column, #ad-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    max-width: 300px;
    margin: 20px auto;
    background-color: rgba(255, 255, 255, 0.3); /* Fondo blanco con 30% de opacidad */
    font-family: 'Roboto', sans-serif; /* Cambiar la fuente */
}

/* Estilos para los elementos de entrada del formulario */
label {
    color: #333;
    display: block;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 5px;
}

input[type="text"], textarea, input[type="tel"], input[type="email"], input[type="number"], input[type="url"] {
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
    font-size: 16px;
    color: #333;
    transition: border 0.3s;
}

input[type="text"]:focus, textarea:focus, input[type="tel"]:focus, input[type="email"]:focus, input[type="number"]:focus, input[type="url"]:focus {
    border: 1px solid #007BFF; /* Cambiar el color del borde al hacer foco */
}

input[type="text"]::placeholder, textarea::placeholder {
    color: #aaa;
}

/* Estilos para el botón de envío */
button[type="submit"] {
    background-color: #007BFF;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 30px;
    transition: background-color 0.4s;
    font-size: 16px;
}

button[type="submit"]:hover {
    background-color: #0056b3;
}